.create_product {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  padding: 0 20px;
}

.upload{
  max-width: 450px;
  height: 500px;
  width: 100%;
  /* border: 1px solid #ddd; */
  padding: 15px;
  margin: 20px;
  position: relative;
  
}

#file_up{
  position: absolute;
  width: 100%;
  height: 100%;
  outline: none;
 border: 3px dotted black;

}

#file_up::before{

  content: "+";
  position: absolute;
  width: 450px;
  height: 100%;
  top: 0;
  left: 0;
  background-color: white;
  color: aqua;
  font-size: 17rem;
  text-align: center;
  margin: 0;
  cursor: pointer;
  
  
}
.file_img{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  
}

 .file_img img {
  
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
}

.file_img span{
  position: absolute;
  top: -13px;
  right: -13px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 50%;
  padding: 6px 10px;
  cursor: pointer;
  font-weight: 900;
  color: red;
}

.create_product form {
  max-width: 500px;
  min-width: 290px;
  width: 100%;
  margin: 15px 30px;
}

.create_product .row {
  width: 100%;
  margin: 15px 0;
}

 .create_product form input,
textarea {
  width: 100%;
  min-height: 40px;
  padding: 0 5px;
  border-radius: 8px;
  border: 1px solid rgb(87, 84, 84);
}

 .create_product form button {
  background: #333;
  color: white;
  margin-top: 5px;
  padding: .25rem .75rem;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-radius: .6rem;
  border-radius: .6rem;
}

 .create_product form option {
  background-color: #333;
  color: white;
}
